@charset "UTF-8";

.promo {
  height: 389px;  // = height of image

  background-color: #8794a1;
  background-image:
    url("../../img/bg-intro-triangle-mobile.svg"),
    url("../../img/bg-back-mobile@1x.jpg");
  background-position:
    bottom 0 left 0,
    center;
  background-size:
    320px 26px,
    cover;  //768px 389px
  background-repeat: no-repeat;

  &--small {
    margin-top: 3px;
    height: 160px;  //155

    background-position:
      bottom 0 left 0,
      top -230px center;  //-235
    background-size:
      320px 26px,
      768px 389px; //768px 389px
  }
}

@media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
  .promo {
    background-image:
      url("../../img/bg-intro-triangle-mobile.svg"),
      url("../../img/bg-back-mobile@2x.jpg");
  }
}

.promo__picture {
  width: 281px;
  height: 214px;
  margin: 0 auto;
  padding-top: 90px;

  &--small {
    width: 229px;
    height: 39px;
    padding-top: 50px;
  }
}

.promo__image {
  width: 100%;
}

@media (min-width: $tablet-width) {
  .promo {
    height: 518px;  // = height of image

    background-color: #8794a1;
    background-image:
      url("../../img/bg-intro-triangle-tablet.svg"),
      url("../../img/bg-back-tablet@1x.jpg");

    background-position:
      bottom 0 left 0,
      center;
    background-size:
      768px 57px,
      cover;  //1200px 518px
    background-repeat: no-repeat;

    &--small {
      margin-top: 0;
      height: 290px;

      background-position:
        bottom 0 left 0,
        top -230px center;
      background-size:
        768px 57px,
        cover;
    }
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    .promo {
      background-image:
        url("../../img/bg-intro-triangle-tablet.svg"),
        url("../../img/bg-back-tablet@2x.jpg");
    }
  }

  .promo__picture {
    padding-top: 77px;
    width: 460px;
    height: 350px;

    &--small {
      height: 80px;
      padding-top: 94px;
    }
  }
}

@media (min-width: $desktop-width) {
  .promo {
    height: 518px;  // = height of image

    background-color: #8794a1;
    background-image:
      url("../../img/bg-intro-triangle-desktop.svg"),
      url("../../img/bg-back-desktop@1x.jpg");
    background-position:
      bottom 0 left 0,
      center;
    background-size:
      1200px 57px,
      cover;  //1200px 518px
    background-repeat: no-repeat;

    &--small {
      height: 301px;
      background-position:
        bottom 0 left 0,
        top -215px center;
      background-size:
        1200px 57px,
        cover;
    }
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    .promo {
      background-image:
        url("../../img/bg-intro-triangle-desktop.svg"),
        url("../../img/bg-back-desktop@2x.jpg");
    }
  }

  .promo__picture {
    &--small {
      padding-top: 102px;
    }
  }
}
